/* Switch for DARK MODE with SUN and MOON */
.switch-dark-mode > span::before {
  content: '\2600';
  color: #ffd557;
}

.switch-dark-mode.ant-switch-checked > span::before {
  content: '\1F319';
}


/* Remove border from graphics */
.custom-donut > .ant-card-body {
  padding: 0;
}

.ant-card.ant-card-bordered.custom-donut {
  border: none;
}

.ant-card.ant-card-bordered.custom-in-line-bar {
  border: none;
}
